* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
}

:root {
  --theme-color: #08c1bb;
  --dashed: 1px solid rgb(232, 235, 235);
  --choice: 2px solid rgb(186, 195, 195);
  --box-shadow: 0 0 0 0.2rem rgba(8, 193, 187, 0.25);
}

::-moz-placeholder {
  color: rgb(171, 173, 173);
}

::placeholder {
  color: rgb(171, 173, 173);
}

.navBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: var(--dashed);
  height: 70px;
  position: relative;
  z-index: 1000;
}
.navBar .logo-box .logo {
  color: #1cc9c3;
  display: flex;
  padding-left: 60px;
  text-decoration: none;
}
.navBar .logo-box .logo .logo-name {
  margin: 5px 0 0 5px;
  font-weight: 900;
}
.navBar .logo-box .logo .logo-name span.eng {
  font-size: 18px;
  text-wrap: nowrap;
}
.navBar .logo-box .logo .logo-name span.zh {
  font-size: 14px;
  text-wrap: nowrap;
}
.navBar .searchBarBox {
  display: flex;
  position: relative;
}
.navBar .searchBarBox .glass {
  position: relative;
  left: 30px;
  top: 15px;
  color: rgba(49, 49, 49, 0.45);
}
.navBar .searchBarBox .searchBar {
  width: 34vw;
  height: 46px;
  padding: 12px 16px 12px 40px;
  background-color: rgba(0, 0, 0, 0.04);
  font-size: 16px;
  border: var(--dashed);
  border-radius: 4px;
  margin-right: 0px;
  position: relative;
}
.navBar .searchBarBox .dropdown-box {
  width: 34vw;
  height: 46px;
  position: absolute;
  top: 100%;
  left: 18px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  z-index: 10;
  background-color: white;
}
.navBar .searchBarBox .dropdown-box ul.dropdown {
  display: flex;
  justify-content: flex-start;
  padding: 4px;
}
.navBar .searchBarBox .dropdown-box ul.dropdown li {
  margin-top: 4px;
  list-style: none;
}
.navBar .searchBarBox .dropdown-box ul.dropdown li button {
  color: var(--theme-color);
  background-color: white;
  border: none;
  padding: 2px 16px;
  margin: 5px;
}
.navBar .searchBarBox .dropdown-box ul.dropdown li button:hover {
  cursor: pointer;
}
.navBar ul.navList {
  display: flex;
  justify-content: space-evenly;
  margin: 0 32px 0 10px;
}
.navBar ul.navList a {
  text-decoration: none;
  flex-wrap: nowrap;
}
.navBar ul.navList li {
  list-style: none;
  border: var(--dashed);
  padding: 8px 16px;
  border-radius: 4px;
  transition: all ease-in-out 0.3s;
  margin: 0 4px;
}
.navBar ul.navList li:hover {
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 0 0.2rem rgba(8, 193, 187, 0.4);
  cursor: pointer;
}
.navBar ul.navList li .listIcon {
